html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  margin: 0px auto;
  width: 100%;
  height: 100%;
  background-color: rgb(80, 79, 76);
  font-family: "Times New Roman", Times, serif;
  overflow-y: hidden;
}

footer {
  width: 10%;
  height: 5%;
}
footer ul {
  color: hsla(0, 0%, 100%, 0.2);
  font-size: 10px;
}
footer ul li {
  margin: 2px;
}

/** Media Queries **/
.nav {
  background-color: #181818;
  width: 30rem;
  height: 100%;
  padding-top: 1rem;
}
.nav .titulo {
  text-align: center;
  color: rgb(3, 99, 16);
  font-size: 3.5rem;
  margin-bottom: 0;
}
.nav .contenedor-superior {
  display: flex;
  justify-content: center;
  width: auto;
  height: 60%;
  flex-direction: column;
  margin: 0 2.5rem;
  border-bottom: 1px solid #FFFFFF;
}
.nav .contenedor-superior .links {
  font-size: 3rem;
  color: #FFFFFF;
  margin: 0.3rem;
  padding: 2rem 0;
}
.nav .contenedor-inferior {
  display: flex;
  justify-content: center;
  width: auto;
  height: 20%;
  flex-direction: column;
  margin: 0 2.5rem;
}
.nav .contenedor-inferior .links2 {
  font-size: 3rem;
  color: #FFFFFF;
  margin: 0.3rem;
  padding: 2rem 0;
}